
.teacher {
    padding-top: 40px;
    padding-bottom: 50px;
     
    
    @media screen and (min-width: 768px) {
        padding-top: 50px;
        padding-bottom: 50px;
    }

    @media screen and (min-width: 1680px) {
        padding-top: 130px;
        padding-bottom: 130px;
     
    }
}

.teacher-main-container {
    padding-left: 20px;
    padding-right: 20px;
}
    
@media screen and (min-width: 320px) {
        .teacher-main-container {
            width: 320px;
            margin: 0 auto;
        }
    }

@media screen and (min-width: 768px) {
        .teacher-main-container {
            width: 768px;
            padding-left: 40px;
            padding-right: 40px;
        }
}

@media screen and (min-width: 1680px) {
        .teacher-main-container {
            width: 1680px;
            padding-left: 183px;
            padding-right: 183px;
        }
}

@media screen and (min-width: 768px) {
    .teacher-container {
        display: flex;
    }
}

.teacher-data-container {
    @media screen and (min-width: 768px) {
        width: 402px;
        margin-left: 40px;
    }

    @media screen and (min-width: 1680px) {
        width: 642px;
        margin-left: 30px;
    }
}

.teacher-title {
    font-weight: 800;
    font-size: 20px;
    line-height: 1.3;
    letter-spacing: 0.03em;
    color: $primary-dark-color;

    margin-bottom: 20px;
    

    @media screen and (min-width: 768px) {
        font-size: 26px;
        line-height: 1.308;
    }

    @media screen and (min-width: 1680px) {
        font-size: 46px;
        line-height: 1.304;
    }
}

.teacher-photo {
    margin-bottom: 20px;
}


.teacher-name {
    font-weight: 800;
    font-size: 20px;
    line-height: 1.2;
    letter-spacing: 0.08em;
    color: $primary-dark-color;

    
    padding-bottom: 10px;
    border-bottom: 1px solid $primary-description-color;
    margin-bottom: 20px;

    @media screen and (min-width: 1680px) {
        font-size: 30px;
        line-height: 1.23;
    }
}

.teacher-list {
    font-weight: 600;
    font-size: 14px;
    line-height: 1.21;
    letter-spacing: 0.06em;
    color: $primary-dark-color;

    height: 193px;
    width: 253px;
    margin-bottom: 21px;
    
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @media screen and (min-width: 768px) {
        height: 165px;
        width: 375px; 
        
        margin-bottom: 18px;
    }

    @media screen and (min-width: 1680px) {
        font-size: 16px;
        line-height: 1.25;
        
        width: 530px;
        margin-bottom: 31px;
    }
}

.teacher-container-icon {
    width: 17px;
    height: 14px;

    margin-right: 9px;

    @media screen and (min-width: 1680px) {
        width: 24px;
        height: 20px;

        margin-right: 35px;
    }
}

@media screen and (max-width: 1579px) {
    .teacher-list-check-mark-2 {
        display: none;
    }
}

@media screen and (min-width: 1680px) {
    .teacher-list-check-mark-2 {
        display: block;
    }
}

@media screen and (max-width: 1579px) {
    .teacher-list-check-mark-1 {
        display: block;
    }
}

@media screen and (min-width: 1680px) {
    .teacher-list-check-mark-1 {
        display: none;
    }
}

.teacher-list-item {
    display: flex;    
}

.teacher-element {
    display: block;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 1.65;
    letter-spacing: 0.08em;
    color: #ff6b09;
}

.teacher-photo-container {
    margin-bottom: 21px;

    @media screen and (min-width: 768px) {
        margin-bottom: 18px;
    }

    @media screen and (min-width: 1680px) {
        margin-bottom: 31px;
    }
}

.teacher-progress-container {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.67;
    color: $primary-description-color;

    @media screen and (min-width: 1680px) {
    font-size: 18px;
    line-height: 1.56;
    }
}

.teacher-progress-list {
    height: 348px;
    margin-bottom: 10px;
    
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @media screen and (min-width: 768px) {
        height: 229px;        
    }

    @media screen and (min-width: 1680px) {
        height: 310px;
        margin-bottom: 10px;
    }
}

.сertificate {
    color: $accent-color;
}